import { useEffect, useState } from 'react';
import { Link, useNavigate } from 'react-router-dom';
import { FiPlus, FiSearch, FiFilter, FiEdit2, FiTrash2, FiRefreshCw } from 'react-icons/fi';
import { getItemsOrdered, searchItems, bulkDeleteItems } from '../../services/itemService';
import { useNotification } from './NotificationSystem';

export default function ItemList() {
  const { showError, showSuccess } = useNotification();
  const [loading, setLoading] = useState(false);
  const [items, setItems] = useState([]);
  const [query, setQuery] = useState('');
  const [filters, setFilters] = useState({ type: '', category: '', status: '' });
  const [selected, setSelected] = useState([]);

  useEffect(() => { fetchData(); }, []);

  const fetchData = async () => {
    try {
      setLoading(true);
      const data = await getItemsOrdered();
      setItems(data);
    } catch (e) {
      console.error(e);
      showError('Failed to load items');
    } finally {
      setLoading(false);
    }
  };

  const runSearch = async () => {
    try {
      setLoading(true);
      const data = await searchItems({ text: query, ...filters });
      setItems(data);
    } catch (e) {
      showError('Search failed');
    } finally { setLoading(false); }
  };

  const toggleSelect = (id) => {
    setSelected((prev) => prev.includes(id) ? prev.filter((x) => x !== id) : [...prev, id]);
  };

  const handleBulkDelete = async () => {
    if (selected.length === 0) return;
    if (!window.confirm(`Delete ${selected.length} item(s)?`)) return;
    try {
      setLoading(true);
      await bulkDeleteItems(selected);
      showSuccess('Items deleted');
      setSelected([]);
      fetchData();
    } catch (e) {
      showError('Delete failed');
    } finally { setLoading(false); }
  };

  return (
    <div className="space-y-4">
      <div className="flex items-center justify-between">
        <div className="flex items-center space-x-2">
          <div className="relative">
            <FiSearch className="absolute left-3 top-1/2 -translate-y-1/2 text-gray-400" />
            <input
              className="pl-9 pr-3 py-2 border rounded-lg w-64"
              placeholder="Search by name/code/HSN"
              value={query}
              onChange={(e) => setQuery(e.target.value)}
              onKeyDown={(e) => e.key === 'Enter' && runSearch()}
            />
          </div>
          <select className="border rounded-lg px-3 py-2" value={filters.type} onChange={(e) => setFilters({ ...filters, type: e.target.value })}>
            <option value="">All Types</option>
            <option value="product">Product</option>
            <option value="service">Service</option>
          </select>
          <select className="border rounded-lg px-3 py-2" value={filters.status} onChange={(e) => setFilters({ ...filters, status: e.target.value })}>
            <option value="">All Status</option>
            <option value="active">Active</option>
            <option value="inactive">Inactive</option>
          </select>
          <button onClick={runSearch} className="px-3 py-2 border rounded-lg flex items-center space-x-2"><FiFilter /><span>Filter</span></button>
          <button onClick={fetchData} className="px-3 py-2 border rounded-lg flex items-center space-x-2"><FiRefreshCw /><span>Refresh</span></button>
        </div>
        <Link to="/admin/items/new" className="px-4 py-2 bg-indigo-600 text-white rounded-lg flex items-center space-x-2">
          <FiPlus />
          <span>Add Item</span>
        </Link>
      </div>

      <div className="bg-white border rounded-lg overflow-hidden">
        <table className="min-w-full">
          <thead className="bg-gray-50">
            <tr>
              <th className="p-3 text-left"><input type="checkbox" onChange={(e) => setSelected(e.target.checked ? items.map(i => i.id) : [])} checked={selected.length>0 && selected.length===items.length} /></th>
              <th className="p-3 text-left">Name</th>
              <th className="p-3 text-left">Type</th>
              <th className="p-3 text-left">Code</th>
              <th className="p-3 text-right">Sale Price</th>
              <th className="p-3 text-right">Tax %</th>
              <th className="p-3 text-left">Status</th>
              <th className="p-3 text-right">Actions</th>
            </tr>
          </thead>
          <tbody>
            {items.map((it) => (
              <tr key={it.id} className="border-t">
                <td className="p-3"><input type="checkbox" checked={selected.includes(it.id)} onChange={() => toggleSelect(it.id)} /></td>
                <td className="p-3">{it.name}</td>
                <td className="p-3 capitalize">{it.type}</td>
                <td className="p-3">{it.code}</td>
                <td className="p-3 text-right">â‚¹{Number(it.pricing?.salePrice || 0).toLocaleString()}</td>
                <td className="p-3 text-right">{Number(it.pricing?.taxRate || 0)}%</td>
                <td className="p-3">{it.status}</td>
                <td className="p-3 text-right">
                  <Link to={`/admin/items/${it.id}/edit`} className="inline-flex items-center px-3 py-1 border rounded mr-2"><FiEdit2 className="mr-1" />Edit</Link>
                </td>
              </tr>
            ))}
            {items.length === 0 && (
              <tr><td className="p-6 text-center text-gray-500" colSpan={8}>No items found</td></tr>
            )}
          </tbody>
        </table>
      </div>

      {selected.length > 0 && (
        <div className="flex justify-between items-center bg-yellow-50 border border-yellow-200 p-3 rounded">
          <span>{selected.length} selected</span>
          <button onClick={handleBulkDelete} className="px-3 py-2 bg-red-600 text-white rounded flex items-center"><FiTrash2 className="mr-2" /> Delete Selected</button>
        </div>
      )}
    </div>
  );
}


